.container {
  max-width: 100%;
  margin: 0 auto;
  padding: 0 20px;

  @media screen and(min-width: 480px) {
    width: 480px;
  }

  @media screen and(min-width: 768px) {
    width: 768px;
    padding: 0 32px;
  }

  @media screen and(min-width: 1200px) {
    width: 1200px;
    padding: 0 20px;
  }
}
